body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.screen {
  width: 100vw;
  height: 100vh;
}

.text {
  position: absolute;
  font-size: 160px;
  text-transform: uppercase;
  font-weight: 900;
  color: #efefef;
}

.layer {
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}